import React,{useState} from 'react';
import { useDispatch } from 'react-redux';
import { increment, decrement } from '../slices/totalPriceSlice';
import { removeItemFromBasket } from '../slices/itemInBasketSlice';
import { TrashIcon } from '@heroicons/react/24/solid';

const ShoppingCardDesktop = ({product}) => {
    const [quantity, setQuantity] = useState(1);
    const dispatch = useDispatch();

    let num = Number(quantity*(product.price)) // The Number() only visualizes the type and is not needed
    let roundedString = num.toFixed(2);
    let roundedPrice = Number(roundedString);


   


    const onBtnPlusClick =() => {
        
        setQuantity(quantity+1);
        dispatch(increment(product.price));
        
    }

    const onBtnLessClick =  () => {
        setQuantity(quantity>0 ? quantity-1 : quantity);
    }




    return (
    <div className='text-sm my-8 flex justify-between items-center gap-5 py-4 border-b border-solid border-gray-200'>
        
                <div className='flex'>
                    {/* image */}
                    
                    <img className='flex items-start w-16 h-auto object-contain' src={product.image} alt=''/>
                    
            
                    {/* info */}
                    <div className='ml-4'>
                            <p className='text-xs py-3 px-2'>{product.title}</p>
                            <strong className='block px-2 text-yellow-600'>${product.price}</strong>
                            {/* quantity */}
                            <div className='flex items-center justify-start py-1 px-2 mt-3 '>
                                 <button onClick={()=> {
                                        onBtnPlusClick();
                                        }}
                                        className=' h-6 w-6 rounded-lg border border-solid border-gray-300 bg-white'>+</button>
                                 <span className='mx-2 '>{quantity}</span>
                                 <button onClick={()=> {
                                        onBtnLessClick();
                                        }} 
                                         className=' h-6 w-6 rounded-lg border border-solid border-gray-300'>-</button>
                            </div>

                    </div>
                </div>

                <div>
                    <TrashIcon onClick={() => {dispatch(removeItemFromBasket(product))}} className='w-12 h-auto px-2 text-red-700 mt-4 cursor-pointer' />
                </div>
           
                
              
           
                {/* total */}
                {/* <div className='flex items-center justify-center  py-1 px-3'>
                <span>Total :</span><strong>${roundedPrice}</strong>
                </div> */}
           
        
        

        
        
    </div>
    );
}



export default ShoppingCardDesktop;