import React from 'react';
import { useDispatch} from 'react-redux';
import { addItemToBasket } from '../slices/itemInBasketSlice';
import { increment } from '../slices/totalPriceSlice';

const Product = ({product}) => {
    const dispatch = useDispatch();

    const onBtnClick = () => {
        dispatch(addItemToBasket(product));
        dispatch(increment(product.price));

    }

    return (
    <div className=' relative flex flex-col m-5 p-10 bg-white items-center justify-center'>
        
            <p className=' absolute top-2 right-2'>{product.category}</p>
       
        <div>
            <img className=' w-52 h-52 object-contain' src={product.image} alt=""/>
            <h3 className=' my-5'>{product.title}</h3>
            <div><strong>{'$'+product.price}</strong></div>
            <button onClick={() => onBtnClick()} className=' bg-yellow-500 py-1 px-6 rounded my-3'>Add to basket</button>
        </div>
        
    
    </div>);
}


export default Product;