import { createSlice } from "@reduxjs/toolkit"


const initialState = {
    value:[]
}
const itemInBasketSlice = createSlice({
    name: 'itemInBasket',
    initialState,
    reducers : {
        addItemToBasket: (state, action) => {
            if(state.value.length ===0){
                state.value.push(action);
            }
            else{
            const arrayOfIds = Array.from(new Set(state.value.map(item => item.payload.id)));
                if(arrayOfIds.indexOf(action.payload.id)=== -1){
                    state.value.push(action);
                } 
            }         
        },
        removeItemFromBasket: (state, action) => {
            const arrayOfIdis = Array.from(new Set(state.value.map(item => item.payload.id)));
            if(arrayOfIdis.indexOf(action.payload.id)!== -1){
                state.value.splice(arrayOfIdis.indexOf(action.payload.id), 1);
            }
                
        }
    }
})


export const selectCount = state => state.itemInBasket.value.length;
export const selectItems = state => state.itemInBasket.value;
export const {addItemToBasket, removeItemFromBasket} = itemInBasketSlice.actions;
export default itemInBasketSlice.reducer;  