import React,{useState} from 'react';
import countries from '../json/countries';
import { useSelector,useDispatch } from 'react-redux';
import { selectIsOpen, updateIsOpen } from '../slices/localModalSlice';
import { updatePosition } from '../slices/positionSlice';

const LocalModal = () => {
    const [positionSelected, setPositionSelected] = useState('United States');
    const dispatch = useDispatch();
    let isOpen = useSelector(selectIsOpen);
    if(!isOpen) return null;
    return (
    <div className=' fixed flex items-center justify-center top-0 left-0 text-sm  min-h-screen w-screen z-50' >
    <div className=' fixed flex items-center justify-center top-0 left-0 text-sm bg-modal__local min-h-screen w-screen z-40' onClick={() => {dispatch(updateIsOpen(false))}}>
    </div>
    <div className=' bg-white w-80 pb-6 rounded flex flex-col justify-center items-center z-50'>
        <h3 className=' bg-gray-100 py-5 border border-solid border-gray-300 px-5 w-full'>Choose your location</h3>
        <div className=' p-5'>
            <p className=' text-xs text-gray-700 my-4'>Delivery options and delivery speeds may vary for different locations</p>
            <select onChange={(event) => {setPositionSelected(event.target.value)}} value={positionSelected} className=' bg-gray-100 py-1 px-4 '>
                {
                    countries.map(country => {
                        return(<option>{country}</option>)
                    })
                }
            </select>
        </div>
        <button onClick={() => {
            dispatch(updateIsOpen(false))
            dispatch(updatePosition(positionSelected))
            }}
             className=' bg-yellow-500 mt-5 px-5 py-2 rounded mx-4'>Done</button>
    </div>
    </div>
    );
}



export default LocalModal;