import React,{useState, useEffect} from 'react';
import { getAuth, signInWithEmailAndPassword, createUserWithEmailAndPassword } from "firebase/auth";
import fire from '../firebase';
import Login from './Login';
import SignUp from './SignUp';

const Authentification = () => {

    const [login, setLogin] = useState(true);
    const [signEmail, setSignEmail] = useState('');
    const [signPassword, setSignPassword] = useState('');
    const [loginEmail, setLoginEmail] = useState('');
    const [loginPassword, setLoginPassword] = useState('');


    
    
    useEffect(() => {
        

        const userAuthentification = () => {
                const auth = getAuth();
                signInWithEmailAndPassword(auth, loginEmail, loginPassword)
                .then((userCredential) => {
                    // Signed in 
                    const user = userCredential.user;
                    console.log('hello MR');
                    // ...
                })
                .catch((error) => {
                    const errorCode = error.code;
                    const errorMessage = error.message;
                });
        }
        userAuthentification();
    }, [loginEmail, loginPassword])

    useEffect(() => {
        
        const userCreation = () => {

            const auth = getAuth();
            createUserWithEmailAndPassword(auth, signEmail, signPassword)
            .then((userCredential) => {
                // Signed in 
                const user = userCredential.user;
                console.log(user);
                // ...
            })
            .catch((error) => {
                const errorCode = error.code;
                const errorMessage = error.message;
                console.log(errorMessage);
                // ..
            });
        }
        userCreation();
    }, [signEmail, signPassword])


    return (
    <div className=' flex justify-center items-center py-14 flex-col'>
        <div className=' w-80 flex flex-col '>
            {/*form */}
            {
                    login && <Login setLoginEmail={setLoginEmail} setLoginPassword={setLoginPassword} login={login} setLogin={setLogin} />
            }
            {
                    !login && <SignUp setSignEmail={setSignEmail} setSignPassword={setSignPassword} login={login} setLogin={setLogin}/>
            }
            
            {/* simple footer */}
            <div>
                    <div className='flex justify-evenly mt-10 mb-4'>
                        <p className='text-blue-500 text-xs cursor-pointer  '>Conditions of Use</p>
                        <p className='text-blue-500 text-xs cursor-pointer '>Privacy Notice</p>
                        <p className='text-blue-500 text-xs cursor-pointer '>Help</p>
                    </div>
                    <p className='text-gray-500 text-xs text-center'>1996-2022, Amazon.com, Inc. or its affiliates</p>
            </div>
        </div>
        
    </div>);
}


export default Authentification;