import React from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import Img1 from '../images/amazon-img1.jpg';
import Img2 from '../images/amazon-img2.jpg';
import Img3 from '../images/amazon-img3.jpg';
import Img4 from '../images/amazon-img4.jpg';
import Img5 from '../images/amazon-img5.jpg';


const Banner = () => {
    return ( 
    <div>
        <Carousel
         autoPlay
         infiniteLoop
         transitionTime={1000}
         showIndicators={false}
         showStatus={false}
         showThumbs={false}
         interval={4000}
         className=' '
         >
            <div>
                <img src={Img1}  alt='img-carousel' />
            </div>

            <div>
                <img src={Img2}  alt='img-carousel'/>
            </div>

            <div>
                <img src={Img3}  alt='img-carousel'/>
            </div>

            <div>
                <img src={Img4} alt='img-carousel' />
            </div>

            <div>
                <img src={Img5} alt='img-carousel' />
            </div>
        </Carousel>
        
    </div>);
}

export default Banner;