import React,{useRef} from 'react';

const Login = ({setLoginEmail, setLoginPassword, login, setLogin}) => {

    const emailInputRef = useRef();
    const passwordInputRef = useRef();

    const userAuthentification = () => {
        setLoginEmail(emailInputRef.current.value);
        setLoginPassword(passwordInputRef.current.value);
        emailInputRef.current.value = '';
        passwordInputRef.current.value = '';

    }


    return (
    <>

        <div className='flex flex-col py-5 px-6 border-gray-300 border-solid border relative'>
            <h1 className=' text-2xl font-semibold mb-5 mt-1'>Sign in</h1>
            <div>
            <label className=' text-xs font-bold'>Email or mobile phone number</label>
            <input ref={emailInputRef} className=' w-full border-solid border border-gray-300 mb-3 mt-1 rounded py-1 px-2' />
            <label className=' text-xs font-bold'>Password</label>
            <input ref={passwordInputRef} className=' w-full border-solid border border-gray-300 mb-3 mt-1 rounded py-1 px-2' />
            <button onClick={() => {userAuthentification()}} className=' block w-full py-1 rounded bg-yellow-500'>Continue</button>
            </div>
            <p className=' text-xs text-gray-500 mt-5 mb-7'>By continuing, you agree to Amazon's <span className=' text-blue-500 cursor-pointer'>Conditions of Use</span> and <span className=' text-blue-500 cursor-pointer'>Privacy Notice</span></p>
            <p className=' text-sm text-blue-500 cursor-pointer'>Need help?</p>    
        </div> 

        <div>
                <div className='flex mt-5 items-center'>
                    <hr className=' border-t-gray-300 flex-grow'/>
                    <p className='mx-2 text-gray-500 text-xs'>New to Amazon?</p>
                    <hr className=' border-t-gray-300 flex-grow'/>
                </div>
                <button onClick={() => {setLogin(!login)}} className='block my-4 w-full py-2 rounded bg-gray-200 text-sm hover:bg-gray-300'>Create your Amazon account</button>
        </div>            
    </>
    );
}


export default Login;