import { configureStore } from "@reduxjs/toolkit";
import basketReducer from '../slices/itemInBasketSlice';
import totalPriceReducer from '../slices/totalPriceSlice';
import localModalSlice from "../slices/localModalSlice";
import positionSlice from "../slices/positionSlice";

const store = configureStore({
    reducer:{
        itemInBasket: basketReducer,
        totalPrice: totalPriceReducer,
        isOpen: localModalSlice,
        position: positionSlice,

    }
});


export default store;