import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    value:false
}

const localModalSlice = createSlice({
    name: 'isOpen',
    initialState,
    reducers : {
        updateIsOpen: (state, action) => {
            state.value = action.payload;
        }      
        
    }
})


export const selectIsOpen = state => state.isOpen.value;
export const {updateIsOpen} = localModalSlice.actions;
export default localModalSlice.reducer;  